.nav-item,
.navbar-brand {
  outline: none;
}

.container {
  padding-bottom: 15px;
  padding-top: 15px;
}

.card-img {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  max-height: 240px;
  overflow: hidden;
}

.card {
  width: 320px;
}

.col-task-preview {
  -ms-flex: 0 0 320px;
      flex: 0 0 320px;
}

.card-img-placeholder {
  background: lightgrey;
  height: 240px;
  width: 320px;
}

.card-img-placeholder > .stripes {
  background:
    -webkit-linear-gradient(bottom right,
      transparent 0%,
      transparent calc(50% - 0.8px),
      dimgrey 50%,
      transparent calc(50% + 0.8px),
      transparent 100%),
    -webkit-linear-gradient(bottom left,
      transparent 0%,
      transparent calc(50% - 0.8px),
      dimgrey 50%,
      transparent calc(50% + 0.8px),
      transparent 100%);
  background:
    -o-linear-gradient(bottom right,
      transparent 0%,
      transparent calc(50% - 0.8px),
      dimgrey 50%,
      transparent calc(50% + 0.8px),
      transparent 100%),
    -o-linear-gradient(bottom left,
      transparent 0%,
      transparent calc(50% - 0.8px),
      dimgrey 50%,
      transparent calc(50% + 0.8px),
      transparent 100%);
  background:
    linear-gradient(to top left,
      transparent 0%,
      transparent calc(50% - 0.8px),
      dimgrey 50%,
      transparent calc(50% + 0.8px),
      transparent 100%),
    linear-gradient(to top right,
      transparent 0%,
      transparent calc(50% - 0.8px),
      dimgrey 50%,
      transparent calc(50% + 0.8px),
      transparent 100%);
  height: 100%;
  position: relative;
  width: 100%;
}

.card-img-placeholder .no-image {
  background: lightgrey;
  left: 50%;
  padding: 10px;
  position: absolute;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.min-h-40 {
  min-height: 40px;
}

.submit-button > img {
  height: 16px;
  margin-left: 5px;
  width: 16px;
}

.submit-button:not(.busy) {
  padding-left: 22.5px;
  padding-right: 22.5px;
}

.submit-button:not(.busy) > img {
  display: none;
}

.tasks-list {
  padding: 15px;
}

.sorting-button {
  margin-left: 5px;
  margin-right: 5px;
}

.logout-button {
  cursor: pointer;
}



